import React, { Component } from 'react'
import Helmet from 'react-helmet'
import Metagenerator from '../../utils/MetaGenerator'

export default class Home extends Component {
  updateMetatags(){
    const { parentURL, name } = this.props;
    let canonicalURL = (parentURL && parentURL !== '') ? `${parentURL}`: '';
    const
      metatagsData = {
        name: name,
        canonicalURL: (canonicalURL && canonicalURL !== '') ? canonicalURL : '',
        prefix: 'home',
        description: 'This is but a silly PoC to try to understand some stuff regarding how SEO works within iframes and SPAs',
      },
      metagenerator = new Metagenerator(metatagsData);

    this.metatags = metagenerator.generateMetatags();
  }

  render() {
    this.updateMetatags();
    return (
      <section className="section">
        <Helmet>
          {this.metatags}
        </Helmet>
        <header className="section__header">
          <h2 className="section__title">Welcome to Chilidex</h2>
        </header>
        <div className="section__content">
          <p className="section__paragraph">This is but a silly PoC to try to understand some stuff regarding how SEO works within iframes and SPA's</p>
          <p className="section__paragraph">Spicy subject we know... <span>🔥</span> <span>🔥</span> <span>🔥</span> </p>
          <p className="section__paragraph">Mei <strong>*da*</strong> Guguele indécse dize!!! </p>

          <p className="section__paragraph">
            Léte da cili sízoune biguíne:
          </p>

          <aside className="section__well">
            <h3 className="section__wellTitle">Dide iu nou? <span>💡</span></h3>
            <p className="section__wellText">Uaile réde xilis caunteín lárege amauntes ove vaitamine Si, ódere spicish caunteín siguenificante amauntes ove of provaitamine Ei beita-caroutine.
            Íne adishoune, pépares àr ei rixe sórce ove vaitamine Bissix.</p>
          </aside>
        </div>
      </section>
    )
  }
}
